#home {
  position: relative;
  background: url('../../assets/bgIMG.png');
  background-position: center;
  background-size: cover;
  background-repeat: repeat;

  .app__wrapper {
    padding: 0;
  }

  .copyright {
    display: none;
  }
}

.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;
  padding: 6rem 2rem 0;

  @media screen and (min-width: 2000px) {
    padding-top: 8rem;
  }
  @media screen and (max-width: 1200px) {
    flex-direction: column;
  }
  @media screen and (max-width: 450px) {
    padding: 6rem 1rem 2rem;
  }
}
.app__header-info {
  display: flex;
  flex: 0.65;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  @media screen and (max-width: 2000px) {
    width: 100%;
    margin-left: 0rem;
  }
}

.app__header-badge {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  .badge-cmp,
  .tag-cmp {
    padding: 1rem 2rem;
    border: var(--white-color);
    border-radius: 15px;
    flex-direction: row;
    width: auto;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  }
  .tag-cmp {
    display: flex;
    flex-direction: column;
    margin-top: 2.5rem;
    p {
      width: 100%;
      text-align: right;
      text-transform: uppercase;
    }
  }
  span {
    font-size: 2.5rem;
    padding-right: 1rem;
    @media screen and (min-width: 2000px) {
      font-size: 5rem;
    }
  }
  @media screen and (max-width: 1200px) {
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.app__header-img {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  position: relative;
  img {
    width: 100%;
    object-fit: contain;
    z-index: 1;
  }

  .overlay-circle {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 90%;
    z-index: 0;
  }
}

.app__header-circles {
  flex: 0.75;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  height: 100%;
  margin-left: 1rem;
  div {
    margin-left: 1rem;
    width: 100px;
    height: 100px;
    background: var(--color-white);
    border-radius: 50%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    img {
      height: 60%;
      width: 60%;
    }
  }
  div:nth-child(1) {
    width: 100px;
    height: 100px;
  }
  div:nth-child(2) {
    width: 150px;
    height: 150px;
    margin-left: 2.5rem;
  }
  div:nth-child(3) {
    width: 80px;
    height: 80px;
  }

  @media screen and (min-width: 2000px) {
    div:nth-child(1) {
      width: 400px;
      height: 400px;
    }
    div:nth-child(2) {
      width: 170px;
      height: 170px;
      margin-left: 2.5rem;
    }
    div:nth-child(3) {
      width: 200px;
      height: 200px;
    }
  }

  @media screen and (max-width: 1200px) {
    flex-direction: row;
    width: 100%;
    flex-wrap: wrap;
    margin-left: 0;
    div {
      margin: 0.2rem;
    }
  }
}
